import React, { Component } from 'react'
import { Container, Media, Input, Row, Col, Button, Navbar, NavbarBrand, ButtonGroup, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap'
import FaWhatsapp from 'react-icons/lib/fa/whatsapp'
import FaGlobe from 'react-icons/lib/fa/globe'
import _ from 'lodash'

import portals from './gyms.json'
import 'bootstrap/dist/css/bootstrap.min.css'

class App extends Component {
	state = {
		onlyGyms: true,
		initialItems: portals,
		items: []
	}

	handleGymCheck = (event) => {
		const { filter, onlyGyms } = this.state

		this.setState({ onlyGyms: !onlyGyms })
		this.applyFilterList(filter, !onlyGyms)
	}

	filterList = (event) => {
		const { onlyGyms } = this.state
		let filter = event.target.value.toLowerCase()

		this.setState({ filter })
		this.applyFilterList(filter, onlyGyms)
	}

	applyFilterList = (filter, onlyGyms) => {
		let updatedList = this.state.initialItems

		updatedList = updatedList.filter(function(item) {
				let val = String(filter).toLowerCase()
				return _.get(item, 'descricao_text', '').toLowerCase().search(val) !== -1 && (!onlyGyms || onlyGyms === _.get(item, 'gym'));
			});
			this.setState({items: updatedList.slice(0, 10)});
	}

	whatsappUrl = (row) => {
		let googleLink = `https://maps.google.com/maps/?daddr=${row.latitude},${row.longitude}`,
			shareText = encodeURIComponent(`*${row.descricao}* \r\n ${googleLink} `)
		return `https://api.whatsapp.com/send?text=${shareText}`
	}

	render() {
		return (
			<div>
				<Navbar color="dark" dark expand="md">
					<NavbarBrand href="/">Ginásios</NavbarBrand>
				</Navbar>
				<Container className="container">
					<div className="filter-list">
						<Row>
							<Col md={{ size: 8, offset: 2 }} xs={12}>
								<InputGroup>
									<Input type="text" placeholder="Pesquise" onChange={this.filterList} />
									<InputGroupAddon addonType="prepend">
										<InputGroupText>
											<Input addon type="checkbox" checked={this.state.onlyGyms} onChange={this.handleGymCheck} /> 
											<small style={{marginLeft: '5px'}}>ginásios?</small>
										</InputGroupText>
									</InputGroupAddon>
								</InputGroup>
								
							</Col>
						</Row>
					
						{
							this.state.items.map((row, index) => (
								<Row>
									<Col md={{ size: 8, offset: 2 }} sm={12} xs={12}>
										<Media key={index} style={{padding: '15px'}}>
											<Media left middle>
												<Media object src={row.image} alt="Imagem" style={{borderRadius: '50%', width: '100px', height: '100px', objectFit: 'cover'}} />
											</Media>

											<Media body  style={{paddingLeft: '15px'}}>
												<Media heading style={{fontSize: '22px'}}>
													{row.descricao}
												</Media>
												<Row>
													<Col sm={12} xs={12} md={8} style={{fontSize: '15px'}}>
														<strong>Endereço:</strong> {row.endereco}
													</Col>

													<Col sm={12} xs={12} md={4}>
														<ButtonGroup className='pull-right' size='lg'>
															<Button color="default" href={`https://www.google.com/maps?daddr=${row.latitude},${row.longitude}`} target='blank'>
																<FaGlobe />
															</Button>

															<Button color="default" href={this.whatsappUrl(row)} target='blank'>
																<FaWhatsapp style={{color: 'green'}}/>
															</Button>
														</ButtonGroup>
													</Col>
												</Row>
											</Media>
										</Media>
									</Col>
								</Row>
							))
						}
					</div>
				</Container>
			</div>
		);
	}
}

export default App;



